@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Noto+Sans:ital,wght@0,100..900;1,100..900&display=swap');

@mixin logo-font {
  font-family: "Poppins", sans-serif;
}

@mixin regular-font {
  font-family: "Noto Sans", sans-serif;
}

@mixin phone {
  @media (max-width: 575.98px) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: 991.98px) {
    @content;
  }
}

@mixin desktop {
  @media (min-width: 992px) {
    @content;
  }
}